.quote {
  font-size: 1.8rem;
  text-align: center;
  color: cornflowerblue;
  transition: 1s;
}
.fa-quote-left::before {
  font-size: 1.5rem;
  margin-right: 0.5rem;
}
.author {
  text-align: end;
  color: cornflowerblue;
  transition: 1s;
}
.fa-twitter::before,
.fa-tumblr::before {
  color: white;
}
.sc {
  height: 36px;
  width: 36px;
  margin: 0 0.2rem;
  border-radius: 4px;
  background: cornflowerblue;
  transition: 1s;
}
.sc a {
  height: 100%;
  display: block;
  text-decoration: none;
}
.sc a i {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.newQuote {
  margin-left: auto;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  background: cornflowerblue;
  color: white;
  font-weight: 600;
  font-size: 0.8rem;
  cursor: pointer;
  transition: opacity 0.12s, background 1s;
}
.newQuote:hover {
  opacity: 0.8;
}
.attribution {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 2rem);
  color: white;
}
.attribution a {
  text-decoration: none;
  color: white;
  font-weight: 600;
}

.row {
  display: flex;
  margin-top: 1rem;
  align-items: center;
}
body {
  transition: 1s;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
